import Layout from './components/layout';
import fetch from 'isomorphic-unfetch';

import Link from 'next/link';
import { Grommet, Heading, Anchor, Text, Box, Layer } from 'grommet';
import { Card } from 'grommet/components';
import { dark } from 'grommet/themes';
import { Disposer } from 'bluebird';


const Items = ({ items }) => {
    return (
        <Grommet theme={dark}>
            <Layout>
                <Box key="wrapper" direction="row-responsive">
                    {items.map(item =>
                        <Card key={item.id} label={item.name} heading={item.name} description={item.description} />
                        //     <Text>{item.name}</Text>
                        //     <Text>{item.description}</Text>
                        //     <Text>${item.price}</Text>
                        //     <Text>{item.category}</Text>
                        // </Box>
                    )}
                </Box>


            </Layout>
        </Grommet>
    )

}

Items.getInitialProps = async ({ req }) => {
    const res = await fetch('http://localhost:8080/api/items');
    // console.log("ressssss", res)
    const json = await res.json();
    return { items: json }
}

export default Items;